/**
 * HomeController
 */
class HomeController {

  /**
   *
   * @param $scope
   * @param SomeService
   */
    // @ngInject
    constructor( $scope, SomeService ) {
    this.name = 'moshe';
    this.SomeService = SomeService;

  }

  /**
   * get data
   */
  get() {
    this.SomeService.getData().then(res => this.data = res.data);
  }

}

export {HomeController};
